
/**
 * 将数组中指定位置的元素移动至目标位置
 * @param targets 目标数组
 * @param from 被移动元素的初始位置
 * @param to 被移动元素的目标位置
 */
function move<T = any>(targets: T[], from: number, to: number) {
  targets.splice(to, 0, targets.splice(from, 1)[0]);
}


/**
 * 删除数组中的指定元素
 * @param targets 目标数组
 * @param target 待删除的元素
 */
function remove<T = any>(targets: T[], target: T) {
  const index = targets.findIndex(item => item === target);
  if (index > -1) {
    targets.splice(index, 1);
  }
}


/**
 * 数组工具函数
 */
export const ArrayUtils = {
  move, remove
};


export default ArrayUtils;
