import * as React from 'react';
import { ArrayUtils } from './index';
import Button from '@material-ui/core/Button';


interface IProps {
  dataSource: any[];
}



export function RemoveDemo(props: IProps) {
  const { dataSource } = props;
  const [originDataSource] = React.useState([...dataSource]);
  const [willBeRemoved, updateNextRemovedValue] = React.useState(getRemovedValue(dataSource));

  return (
    <React.Fragment>
      <h2>ArrayUtils.remove([{dataSource.toString()}], '{willBeRemoved}')</h2>
      <div>{dataSource.join(',')}</div>
      <Button variant="outlined" color='primary'  onClick={() => {
        ArrayUtils.remove(dataSource, willBeRemoved);
        updateNextRemovedValue(getRemovedValue(dataSource));
      }}
      >点击移除数组中的 {willBeRemoved}</Button>

      <Button variant="outlined" color='primary'  onClick={() => {
        dataSource.length = 0;
        originDataSource.forEach(item => dataSource.push(item));
        updateNextRemovedValue(getRemovedValue(dataSource));
      }}>重置</Button>

    </React.Fragment>
  );
}


function getRemovedValue(array: any[]) {
  return array[Math.floor((Math.random() * 100)) % array.length];
}
